import { Util } from '@/config/util';

/**
 * 拉取顾客列表
 * @param {Object} data 请求参数
 * @param {Function} callback 请求成功回调
 */
export const customerList = function (data, callback) {
    Util.DataEncSubmit('customer_get.php', data, (resp) => {
        if (callback && typeof callback === "function") {
            callback(resp);
        }
    });
};

/**
 * 保存 顾客列表
 */
export const saveCustomer = function (data, callback) {
    Util.DataEncSubmit('customer_save.php', data, (resp) => {
        if (callback && typeof callback === "function") {
            callback(resp);
        }
    });
};


